@import url(https://fonts.googleapis.com/css2?family=Akshar:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
}

body {
  font-family: 'Akshar', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eee;
  color: #252525;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

button {
  cursor: pointer;
}

button:disabled {
  cursor: default;
}

#root {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.loader {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

::selection {
  background-color: rgba(180, 180, 180, 0.4);
}

-moz::selection {
  background-color: rgba(128, 128, 128, 0.5);
}

::-webkit-scrollbar {
  height: 15px;
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background: rgba(85, 85, 85, 0.5);
}

::-webkit-scrollbar-track {
  background: rgba(85, 85, 85, 0.2);
}

.home-page {
  margin-bottom: 50px;
}

.products {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 0;
}

.products .product-card {
  margin: 25px;
}

.home-initial-message {
  margin: 10px;
  text-align: center;
}

@media screen and ( min-width : 576px ) {

  .home-page {
    display: flex;
  }

  .products {
    width: calc(100% - 250px);
  }
}

@media screen and ( min-width : 992px ) {

  .products {
    max-width: 1260px;
    width: calc(100% - 300px);
  }
}

@media screen and ( min-width : 1245px ) {

  .products {
    justify-content: space-around;
    margin: 10px auto;
    max-width: 1000px;
  }

  .products .product-card {
    margin: 15px;
  }
}

@media screen and ( min-width : 1660px ) {

  .products {
    width: calc(100% - 450px);
  }
}

/* stylelint-disable declaration-no-important */
.product-page {
  background-color: white;
  flex: 1 1;
}

.product-page .product-main {
  margin-bottom: 20px;
  padding: 0 15px;
}

.product-page .product-pictures {
  margin: auto;
  width: 100%;
}

.product-page .carousel-image {
  height: 100%;
  width: 100%;
}

.product-page .carousel-btn {
  background: none ! important;
}

.product-page .carousel-btn svg {
  color: #252525 ! important;
}

.product-page .product-info {
  padding: 10px 0;
}

.product-page .product-condition-and-sold-quantity {
  color: #707070;
  font-size: 12px;
  margin-bottom: 5px;
  text-align: end;
}

.product-page .product-title {
  color: #383838;
  font-size: 25px;
}

.product-page .product-price-discount {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
}

.product-page .original-price {
  color: #999;
  flex-basis: 100%;
  font-size: 15px;
  text-decoration: line-through;
}

.product-page .current-price, .product-price {
  font-size: 30px;
}

.product-page .product-price-discount, .product-page .product-price {
  margin: 12px 0;
}

.product-page .product-off {
  color: #2b912b;
  margin-left: 5px;
}

.product-page .product-buy {
  border-top: 1px solid #959595;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  padding-top: 12px;
}

.product-page .product-free-shipping {
  align-items: center;
  color: #2b912b;
  display: flex;
  font-size: 15px;
  font-weight: 500;
  grid-gap: 3px;
  gap: 3px;
}

.product-page .product-details {
  margin: 50px 0 10px;
  padding: 0 20px;
}

.product-page .product-description {
  padding: 10px 0;
}

.product-page .description-title {
  font-weight: 600;
  margin: 10px 0;
}

.product-page .border {
  background-color: #959595;
  height: 1px;
  margin: auto;
  width: 95%;
}

@media screen and ( min-width : 508px ) {

  .product-page .product-pictures {
    width: 500px;
  }

  .product-page .carousel-image {
    height: 500px;
    width: 500px;
  }
}

@media screen and ( min-width : 576px ) {

  .product-page .product-info {
    padding: 10px 25px;
  }

  .product-page .product-buy {
    width: 350px;
  }

  .product-page .product-details {
    padding: 0 30px;
  }
}

@media screen and ( min-width : 992px ) {

  .product-page {
    margin: auto;
    max-width: 1100px;
    min-width: 992px;
  }

  .product-page .product-main {
    display: flex;
    justify-content: center;
  }

  .product-page .product-pictures {
    margin: 0;
    padding-top: 10px;
    width: 500px;
  }

  .product-page .product-info {
    max-width: 575px;
    padding-left: 50px;
    padding-top: 40px;
    width: calc(100% - 500px);
  }

  .product-page .product-details {
    margin: 50px auto 30px;
    max-width: 1100px;
    position: relative;
  }

  .product-page .product-attributes {
    bottom: 0;
    position: absolute;
    top: 0;
    width: 55%;
  }

  .product-page .product-attributes .responsive-table {
    bottom: 0;
    max-height: unset;
    position: absolute;
    top: 41px;
    width: 100%;
  }

  .product-page .product-description {
    margin-left: calc(55% + 10px);
    min-height: 250px;
    padding: 0 12px 0 50px;
  }

  .product-page .description-title {
    font-size: 18px;
    margin: 0 0 10px;
  }

  .product-page .product-attributes::-webkit-scrollbar-track {
    background: none;
  }
}

.cart-page {
  align-items: center;
  background-color: white;
  display: flex;
  flex: 1 1;
  flex-direction: column;
  padding-bottom: 15px;
}

.shopping-cart-empty-message {
  text-align: center;
}

.cart-page .cart-title {
  border-bottom: 1px solid #606060;
  font-size: 25px;
  font-weight: 500;
  margin: 15px 0 10px;
  padding-bottom: 10px;
  text-align: center;
  width: 70%;
}

.cart-page .cart-items {
  width: 100%;
}

.cart-page .cart-items ul {
  align-items: center;
  background-color: white;
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: 0;
  width: 100%;
}

.cart-page .cart-total-price {
  font-size: 18px;
  margin: 15px 0 0;
}

.cart-page .cart-checkout {
  margin: 15px 0;
}

@media screen and ( min-width : 768px ) {

  .cart-page {
    margin: auto;
    padding: 0 20px;
  }

  .cart-page .cart-items ul {
    max-width: 800px;
  }
}

@media screen and ( min-width : 768px ) {

  .shopping-cart-empty-message {
    padding: 0 30px;
    width: 708px;
  }
}

.checkout-page {
  align-items: center;
  align-self: center;
  background-color: white;
  display: flex;
  flex: 1 1;
  flex-direction: column;
  max-width: 992px;
  padding-bottom: 15px;
  grid-row-gap: 20px;
  row-gap: 20px;
}

.checkout-page .checkout-title {
  width: 100%;
}

.checkout-page .checkout-title p {
  border-bottom: 1px solid #606060;
  font-size: 25px;
  font-weight: 500;
  margin: 15px auto 10px;
  padding-bottom: 10px;
  text-align: center;
  width: 70%;
}

.checkout-page .checkout-products {
  margin-bottom: 20px;
  padding: 0 15px;
}

.checkout-page .checkout-products ul {
  padding: 0;
}

.checkout-page .checkout-form {
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
  gap: 15px;
  padding: 0 20px;
  width: 90%;
}

.checkout-page .checkout-buy {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 25px 0 15px;
  width: 100%;
}

.checkout-page .checkout-buy p {
  font-size: 18px;
  margin-bottom: 10px;
}

.checkout-page .checkout-buy button {
  width: 200px;
}

@media screen and ( min-width : 576px ) {

  .checkout-page .checkout-form {
    width: 570px;
  }
}

@media screen and ( min-width : 992px ) {

  .checkout-page {
    flex-flow: row wrap;
    justify-content: center;
    padding-bottom: calc(100vh - 750px);
  }

  .checkout-page .checkout-products {
    height: 429px;
    margin-bottom: 0;
    overflow: overlay;
    padding: 0;
  }

  .checkout-page .checkout-form {
    padding-left: 30px;
    width: 500px;
  }

  .checkout-page .checkout-buy {
    margin-top: 15px;
  }
}

